@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
    width: min(95%, 140rem);
    margin: 0 auto;
}
@mixin contenedor-md {
    width: min(95%, 80rem);
    margin: 0 auto;
}
@mixin contenedor-sm {
    width: min(95%, 60rem);
    margin: 0 auto;
}
@mixin headings{
    font-family: v.$fuente-principal;
    font-weight: v.$black;
    margin: 2rem 0;
}
@mixin  nombre-pagina {
    @include headings;
    color: v.$amarillo;
    text-align: center;
   margin: 6rem 0 3rem 0;
}
@mixin descripcion-pagina {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$delgada;
    color: v.$gris-oscuro;
    margin-bottom: 5rem;
}
@mixin texto-fluido($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw  ,$max);
}
@mixin texto-degradado($inicio, $final){
    background: linear-gradient(to right, $inicio, $final);
    background-clip: text;
   color: transparent;
}
@mixin logo{
    color: v.$blanco;
    text-align:center;
    margin-bottom: 2rem;
    @include desktop {
        text-align: left;
        margin-bottom: 0;
    }
}
@mixin menu-social($color, $size) {
    &__enlace {

        &::before{
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smooting: antialiased;
            font-family: "Font Awesome 6 Brands";
        }
        // &[href*="https://facebook.com"]::before{
        //     content: "\f39e";
        // }

    }
    &__ocultar {
        display: none;
    }
}

@mixin enlace{
    all:unset;
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;

    &:hover{
        color: v.$amarillo;
        cursor: pointer;
    }

}
@mixin shadow {
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}
@mixin  reset-lista {
    margin: 0;
    padding: 0;
    list-style: none;  
}
@mixin card {
    border: 1px solid lighten(v.$amarillo, 10);
    padding: 3rem;

    &__nombre {
        font-weight: v.$black;
        font-size: 2.6rem;
        margin: 2rem 0;
    }
}

